import { FactConatiner } from '../components/FactContainer';
import { getWidthOfFactContainer, SCREEN_HEIGHT, SCREEN_WIDTH } from '../constants/sizes';
import { PageTitle } from '../components/PageTitle';
import './HomePage.style.css';
import { COLORS } from '../constants/colors';


export const HomePage = () => {

    return (
        <div id='homepage-body'>
            <div id='homepage-header-container-left' style={{ 
                'width' : SCREEN_WIDTH,
                'height' : SCREEN_WIDTH,
                'top' : `-${SCREEN_HEIGHT * 13 / 10}px`,
                'left' : `-${SCREEN_WIDTH * 3 / 5}px` 
            }}>

            </div>
            <div id='homepage-header-container-right' style={{ 'color' : COLORS.white, 'height' : SCREEN_HEIGHT }}>
                <div class='navigation-item-container'>
                    <p class='navigation-item-text'>Sup</p>
                </div>
                <div class='navigation-item-container'>
                    <p class='navigation-item-text'>Sup</p>
                </div>
                <div class='navigation-item-container'>
                    <p class='navigation-item-text'>Sup</p>
                </div>
                <div class='navigation-item-container'>
                    <p class='navigation-item-text'>Sup</p>
                </div>
                <div class='navigation-item-container'>
                    <p class='navigation-item-text'>Sup</p>
                </div>
            </div>
            <div id='homepage-main-info-container' style={{ 'width' : getWidthOfFactContainer(), 'top' : SCREEN_HEIGHT / 8, 'right' : SCREEN_WIDTH / 4}}>
                <div id='homepage-main-info-title'>
                    <PageTitle title={'TJ.'} />
                </div>
                
                <div id='homepage-main-info-body'>
                    <FactConatiner
                        subtitle={'My name is'}
                        title={'TRISTAN JONG'}
                        text={'I am a Software Engineer that has had many years of experience with development of Web Apps and Phone Apps ranging from personal projects to work experiences. I actively look for any opportunity I can get to further develop my personal skills and knowledge'}
                    />
                </div>
            </div>
        </div>
    );
}